<template>
  <v-app id="inspire">
    <v-parallax dark src="">
      <v-content>
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="64"></v-progress-circular>
        </v-overlay>
        <v-container class="fill-height" fluid>
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <v-card class="elevation-12">
                <v-toolbar color="primary" dark flat>
                  <v-toolbar-title>2ª via de Boletos - Clientes</v-toolbar-title>
                  <v-spacer />
                </v-toolbar>
                <v-card-text>
                  <v-form>
                    <v-text-field
                      v-model="raizCnpj"
                      label="Raiz CNPJ"
                      name="login"
                      required
                      prepend-icon="mdi-account"
                      type="text"
                    />

                    <v-text-field
                      v-model="senha"
                      id="password"
                      label="Senha"
                      name="password"
                      required
                      prepend-icon="mdi-lock"
                      type="password"
                    />
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn @click="doLogin()" color="primary">Entrar</v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-content>
    </v-parallax>
  </v-app>
</template>
<script>
export default {
  data() {
    return {
      raizCnpj: "",
      senha: "",
      overlay: false
    };
  },
  methods: {
    doLogin() {
      if (this.senha.length > 0) {
        this.overlay = true;
        this.$http
          .post("http://localhost:3000/login", {
            raizCnpj: this.raizCnpj,
            senha: this.senha
          })
          .then(response => {
            let is_admin = response.data.user.is_admin;
            localStorage.setItem("user", JSON.stringify(response.data.user));
            localStorage.setItem("jwt", response.data.token);

            if (localStorage.getItem("jwt") != null) {
              this.$emit("loggedIn");
              if (this.$route.params.nextUrl != null) {
                this.$router.push(this.$route.params.nextUrl);
              } else {
                if (is_admin == 1) {
                  this.$router.push("home");
                } else {
                  this.$router.push("login");
                }
              }
            }
          })
          .catch(function(error) {
            console.error(error.response);
          })
          .finaly(() => {
            this.overlay = false;
          });
      }
    }
  }
};
</script>